@import url('https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800,900&display=swap');
body
{
    margin: 0;
    padding: 0;
    background: #383838;
    font-family: "Poppins", sans-serif;
}
section
{
    width: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background: #383838;
}
.swiper-slide
{
    width: 400px;
    &:nth-child(1) .sliderText
    {
        background: #2196f3;
    }
    &:nth-child(2) .sliderText
    {
        background: #e91e63;
    }
    &:nth-child(3) .sliderText
    {
        background: #c3d41a;
    }
    &:nth-child(4) .sliderText
    { 
        background: #ff9800;
    }
    &:nth-child(5) .sliderText
    {
        background: #c33ada;
    }
}

.card
{
    position: relative;
    background: #fff;
    width: 400px;
    height: 5 00px;
    margin: 0 auto;
    .content
    {
        width: 400px;
        padding: 30px;
        box-sizing: border-box;
        a
        {
            display: inline-block;
            margin: 10px 0 0;
            padding: 10px 20px;
            text-decoration: none;
            border: 2px solid #262626;
            color: #262626;
            font-weight: 600;
        }
    }
    .sliderText
    {
        position: relative;
        width: 100%;
        height: 200px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #000;
        h3
        {
            color: #fff;
            font-size: 3em;
        }
    }
}